import { isGameOver } from "./gameStatusCheck";
import { addRandomTile, body } from "../app";
import { showModal } from "./modal";
import { moveTiles, score } from "./tilesMove";
import { setItemsInStorage } from "./raitings";

const now = new Date();
const currentTime = formatDateTime(now);
let raitings = [];

function onDirectionKeyPress(e) {
  let movePossible;
  switch (e.key) {
    case "ArrowUp":
      playClick();
      movePossible = moveTiles(1, 0);
      break;
    case "ArrowDown":
      playClick();
      movePossible = moveTiles(-1, 0);
      break;
    case "ArrowLeft":
      playClick();
      movePossible = moveTiles(0, -1);
      break;
    case "ArrowRight":
      playClick();
      movePossible = moveTiles(0, 1);
      break;
  }
  if (movePossible) {
    addRandomTile();
    let gameOver = isGameOver();
    console.log("is gameover", gameOver.gameOver);
    if (gameOver.gameOver) {
      let raitItem = {
        score: score,
        currentTime: currentTime,
      };
      raitings.push(raitItem);
      setItemsInStorage(raitings);
      showModal(gameOver.message);
    }
  }
}

function playClick() {
  const clickSound = document.createElement("audio");
  const clickSource = document.createElement("source");
  clickSource.src =
    "./157776__hiddenpersuader__click-sound-from-a-ps9-circuit-bent-machine.wav";
  clickSound.append(clickSource);
  body.append(clickSound);

  clickSound.play();
}

function formatDateTime(date) {
  const day = String(date.getDate()).padStart(2, "0");
  const month = String(date.getMonth() + 1).padStart(2, "0");
  const year = date.getFullYear();
  const hours = String(date.getHours()).padStart(2, "0");
  const minutes = String(date.getMinutes()).padStart(2, "0");

  return `${day}.${month}.${year} ${hours}:${minutes}`;
}

export { onDirectionKeyPress, raitings, currentTime };
